import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './Button.css';


export class Button extends Component {

  render() {
    const { type } = this.props;
    switch (type) {
      case 'link':
        return this.renderLink();
      case 'file':
        return this.renderFileInput();
      default:
        return this.renderButton();
    }
  }


  renderFileInput() {
   const { text } = this.props;

   return <label className="Button">
    { text }
    <input type="file"
      className="Button__input_file"
      onChange={this.onFileInputChanged.bind(this)}
    />
   </label>;
  }


  renderLink() {
    const { href, downloadFileName, text } = this.props;
    const linkProps = {
      download: downloadFileName || null
    };

    return <a href={ href }
      { ...linkProps}
      className="Button"
    >
      { text }
    </a>;
  }


  renderButton() {
    const { type, text, isDisabled } = this.props;
    const className = `Button ${isDisabled ? 'Button_disabled' : ''}`;
    const btnProps = {
      type: type || 'button',
      disabled: isDisabled ? 'disabled' : null
    };

    return <button {...btnProps}
      onClick={this.onBtnPress.bind(this)}
      className={className}
    >
      { text }
    </button>;
  }


  onFileInputChanged(event) {
    const files = event.target.files;
    const file = files && files[0];
    this.props.onFileSelected(file);
  }


  onBtnPress() {
    const { onPress } = this.props;
    if (onPress) {
      onPress();
    }
  }

}


Button.propTypes = {
  // Если задан href, то вместо кнопки будет отрисована ссылка в том же дизайне,
  // параметра href будет прописан в href ссылки.
  // Параметры isDisabled, type, onPress будут проигнорированы.
  href: PropTypes.any,
  // Если отрисовывается ссылка, то значение этого параметра будет прописано
  // в атрибут download этой ссылки, чтобы задать имя выгружаемого файла.
  downloadFileName: PropTypes.string,
  isDisabled: PropTypes.bool,
  // Помимо кнопочных типов button (по умолчанию) и submit поддерживаются типы:
  // file - будет отрисован input в дизайне кнопки,
  // link - будет отрисована ссылка в дизайне кнопки.
  type: PropTypes.string,
  onPress: PropTypes.func,
  // Для кнопок с type=file функция будет вызвана после того, как пользователь выберет файл.
  // В функцию будет передан объект выбранного файла.
  onFileSelected: PropTypes.func,
  text: PropTypes.string
};



