/**
 * Returns correct declansion for given number.
 * @param {number} num
 * @param {string} formOne
 * @param {string} formTwo
 * @param {string} formTen
 * @param {string} [formZero] formTen will be used if formZero is not provided.
 */
export function getDeclension(num, formOne, formTwo, formTen, formZero) {
  num = Math.abs(num);
  var nMod10 = num % 10;
  var nMod100 = num % 100;

  if (!num && formZero) {
      return formZero;
  }
  if (num === 1 || (nMod10 === 1 && nMod100 !== 11)) {
      return formOne;
  }
  if (nMod10 > 1 && nMod10 < 5 && nMod100 !== 12 && nMod100 !== 13 && nMod100 !== 14) {
      return formTwo;
  }
  return formTen;
}


/**
 * Дополняет заданными символами слева до нужной длины строки.
 * @see https://developer.mozilla.org/ru/docs/Web/JavaScript/Reference/Global_Objects/String/padStart
 * @param {string|number} item Параметр будет явно приведён к строке.
 * @param {number} strLength Нужная длина строки.
 * @param {string} symbol Символы, которыми будем дополнять.
 * @returns {string}
 */
export function padStart(item, strLength, symbol) {
  const strItem = '' + item;
  return strItem.padStart(strLength, symbol);
}



