import { defaultL10n } from './default-l10n';


const PREFIX = 'L10N';
const storageKey = 'l10n';


class L10N {

  constructor() {
    this.customL10N = this._restoreFromStorage() || {};
  }


  _restoreFromStorage() {
    const storedL10n = window.localStorage.getItem(storageKey);
    if (!storageKey) {
      return null;
    }

    try {
      return JSON.parse(storedL10n);
    } catch (e) {
      console.log(PREFIX, 'error while parsing l10n from local storage:');
      console.log(PREFIX, 'value', storedL10n);
      console.log(PREFIX, 'error', e);
    }

    return null;
  }


  /**
   * Получение текста для отображения по его id.
   * @param {string} id
   * @returns {string}
   */
  getText(id) {
    return this.customL10N[id] || defaultL10n[id] || id;
  }


  /**
   * Получение объекта с текстами по заданным ключам.
   * @param {string} ...ids
   * @returns {object}
   */
  getTexts(...ids) {
    return ids.reduce((memo, id) => {
      memo[id] = this.getText(id);
      return memo;
    }, {});
  }


  /**
   * Получение локализации для экспорта.
   * @returns {object}
   */
  getTextsForExport() {
    return Object.assign({}, defaultL10n, this.customL10N);
  }


  importTexts(object) {
    this.customL10N = object;
    window.localStorage.setItem(storageKey, JSON.stringify(object));
  }


  resetTexts() {
    window.localStorage.removeItem(storageKey);
  }

}


export const l10n = new L10N();



