import { API } from './../api';

export const auth = Object.freeze({

  askAdminPassword() {
    return new Promise((resolve, reject) => {
      const password = prompt('Пожалуйста, введите админский пароль');
      const adminPassword = API.getAdminPassword();
      if (adminPassword === password) {
        resolve();
      } else {
        reject();
      }
    });
  }

});



