/**
 * @typedef Question
 * @property {string} text Текст вопроса.
 * @property {string} img Необязательный. Урл до картинки для вопроса.
 * @property {Answer[]} answers Варианты ответов.
 * @property {number} __index Внутренний индекс вопроса, будет сгенерирован или переписан при импорте.
 */

/**
 * @typedef Answer
 * @property {string} text Текст ответа.
 * @property {Reaction[]} reactions Набор реакций на ответы.
 * Случайным образом будет выбрана одна реакция.
 * @property {number} __index Внутренний индекс ответа, будет сгенерирован или переписан при импорте.
 */

/**
 * @typedef Reaction
 * Для реакции может быть задан либо sticker, либо img, но не оба вместе!
 * @property {string} text Текст реакции.
 * @property {string} sticker Необязательный. ID стикера.
 * @property {string} img Необязательный. Урл до картинки для реакции.
 * @property {string} forcedFinalState Необязательный. Признак фатальной реакции,
 * после которой пользователь принудительно будет отправлен на результаты и закончит игру.
 * Пользователь может быть направлен на фиксированный результат вне зависимости от набранных
 * очков (см. значения в finalState) или на вычисляемый результат (значение auto).
 * См. src/app/finalState.
 * @property {number} aValue Цена реакции.
 * @property {number} bValue Цена реакции.
 */


// Каждый question/reaction может содержать опциональные поля:
