import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { actions } from '../../redux';
import PropTypes from 'prop-types';
import { Button } from './../Button';
import { FileExport } from './../FileExport';
import { settings, mimeTypes, readFileAsString, l10n } from './../../helpers';


class _AdminSettings extends Component {

  render() {
    return <div className="block AdminSettings">
      <div className="indent_vert">
        <FileExport prepareText="Подготовить настройки к экспорту"
          downloadText="Экспортировать"
          getFileDescription={this.getSettingsFileDescription.bind(this)}
        />
      </div>
      <div className="indent_vert">
        <Button text="Импорт настроек из файла"
          type="file"
          onFileSelected={this.onImportSettings.bind(this)}
        />
      </div>
      <div className="indent_vert">
        <Button text="Очистить локальные настройки"
          onPress={this.clearSettings.bind(this)}
        />
      </div>
      <div className="indent_vert">
        <FileExport prepareText="Подготовить локализацию к экспорту"
          downloadText="Экспортировать"
          getFileDescription={this.getL10nFileDescription.bind(this)}
        />
      </div>
      <div className="indent_vert">
        <Button text="Импорт локализации из файла"
          type="file"
          onFileSelected={this.onImportLocalization.bind(this)}
        />
      </div>
      <div className="indent_vert">
        <Button text="Очистить кастомную локализацию"
          onPress={this.clearL10n.bind(this)}
        />
      </div>
      <div className="indent_vert">
        <Button text="Вернуться к общим результатам"
          onPress={this.backToResults.bind(this)}
        />
      </div>
    </div>;
  }


  clearSettings() {
    if (window.confirm('Вы действительно хотите сбросить локальные настройки?')) {
      settings.clearSettings();
      alert('Локальные настройки сброшены! Страницу будет перезагружена!');
      window.location.reload();
    }
  }


  onImportSettings(file) {
    readFileAsString(file)
      .then(fileContent => {
        const incoming = JSON.parse(fileContent);
        settings.setSettingsByImport(incoming);
        alert('Настройки успешно применены! Страницу будет перезагружена!');
        window.location.reload();
      })
      .catch(e => {
        alert(`Ошибка чтения файла ${file.name}: ${e}`);
      });
  }


  onImportLocalization(file) {
    readFileAsString(file)
      .then(fileContent => {
        const incoming = JSON.parse(fileContent);
        l10n.importTexts(incoming);
        alert('Локализация успешно применена! Страницу будет перезагружена!');
        window.location.reload();
      })
      .catch(e => {
        alert(`Ошибка чтения файла ${file.name}: ${e}`);
      });
  }


  clearL10n() {
    if (window.confirm('Вы действительно хотите сбросить кастомную локализацию?')) {
      l10n.resetTexts();
      alert('Кастомная локализация сброшена! Страницу будет перезагружена!');
      window.location.reload();
    }
  }


  /**
   * @returns {object}
   */
  getL10nFileDescription() {
    const l10nObject = l10n.getTextsForExport();
    const content = JSON.stringify(l10nObject, null, 4);
    const name = 'game-l10n.json';
    return {
      content,
      name,
      type: mimeTypes.json
    };
  }


  /**
   * @returns ExportedFileDescription
   */
  getSettingsFileDescription() {
    const settingsObject = settings.getSettingsForExport();
    const content = JSON.stringify(settingsObject, null, 4);
    const name = 'game-settings.json';
    return {
      content,
      name,
      type: mimeTypes.json
    };
  }


  backToResults() {
    this.props.actions.fetchResults();
  }

}


_AdminSettings.propTypes = {
  actions: PropTypes.object
};


function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}


export const AdminSettings = connect(null, mapDispatchToProps)(_AdminSettings);



