import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { actions } from '../../redux';
import PropTypes from 'prop-types';
import './AdminResults.css';
import { Button } from './../Button';
import { FileExport } from './../FileExport';
import { mimeTypes, getCSVDelimiter, getFileName, l10n } from './../../helpers';


class _AdminResults extends Component {

  render() {
    const results = this.props.results.slice().sort((a, b) => b.score - a.score);
    const texts = l10n.getTexts('results', 'points', 'delete', 'prepareResultsForExport', 'downloadCsv',
      'backToRegularResults');

    return <div className="block AdminResults">
      <div className="AdminResults__header">
        { texts.results }
      </div>
      <ul className="AdminResults__list">
        { results.map((result, index) => <li className="indent_vert_half AdminResults__result"
            key={result.id}
          >
            <span className="AdminResults__index AdminResults__info-item">
              { index + 1 }
            </span>
            <span className=" AdminResults__info-item">
              {texts.points}:&nbsp;
              <span className="AdminResults__score">
                { result.score }
              </span>
            </span>
            <span className="AdminResults__info-item">
              { result.name }
            </span>
            <span className="AdminResults__info-item">
              { result.phone }
            </span>
            <span className="AdminResults__remove AdminResults__info-item"
              data-index={index}
              onClick={this.removeResult.bind(this)}
            >
              { texts.delete }
            </span>
          </li>)
        }
      </ul>
      <div className="indent_vert">
        <FileExport prepareText={texts.prepareResultsForExport}
          downloadText={texts.downloadCsv}
          getFileDescription={this.getFileDescription.bind(this, results)}
        />
      </div>
      <div className="indent_vert">
        <Button text={texts.backToRegularResults}
          onPress={this.backToRegularResults.bind(this)}
        />
      </div>
    </div>;
  }


  /**
   * @returns {ExportedFileDescription}
   */
  getFileDescription(results) {
    return {
      content: this.getResultsFileContent(results),
      name: getFileName('results', 'csv'),
      type: mimeTypes.csv
    }
  }


  getResultsFileContent(results) {
    const delimiter = getCSVDelimiter();
    const headerRow = [
      '#',
      'Score',
      'Name',
      'Phone'
    ].join(delimiter);
    const resultsRows = results.map((result, index) => [
      index + 1,
      result.score,
      `"${result.name.replace(new RegExp(/"/, 'g'), "'")}"`,
      // Таким образом мешаем Excel форматировать номер телефона в научную нотацию чисел.
      `"# ${result.phone}"`
    ].join(delimiter));
    const rows = [headerRow, ...resultsRows];
    const fileContent = rows.join('\n');
    return fileContent;
  }


  backToRegularResults() {
    this.props.actions.fetchResults();
  }


  removeResult(event) {
    const resultIndex = event.target.dataset.index;
    const result = this.props.results[resultIndex];
    const confirmMsg = `Вы действительно хотите удалить результат пользователя\n`
      + `${result.name} (${result.phone}), очков ${result.score}?`;
    if (window.confirm(confirmMsg)) {
      this.props.actions.removeResult(result);
    }
  }

}


_AdminResults.propTypes = {
  actions: PropTypes.object,
  results: PropTypes.array
};


function mapStateToProp(state) {
  return {
    results: state.results
  };
}


function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}


export const AdminResults = connect(mapStateToProp, mapDispatchToProps)(_AdminResults);



