import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { actions } from '../../redux';
import { Button } from './../Button';
import { FileExport } from './../FileExport';
import { mimeTypes, getFileName } from './../../helpers';
import { stats } from './../../api';


class _AdminStats extends Component {

  render() {
    return <div className="block AdminStats">
      <div className="indent_vert">
        <FileExport prepareText="Подготовить статистику к выгрузке"
          downloadText="Скачать JSON"
          getFileDescription={this.getFileDescription.bind(this)}
        />
      </div>
      <div className="indent_vert">
        <Button text="Сбросить статистику"
          onPress={this.resetStats.bind(this)}
        />
      </div>
      <div className="indent_vert">
        <Button text="Вернуться к общим результатам"
          onPress={this.backToRegularResults.bind(this)}
        />
      </div>
    </div>;
  }


  resetStats() {
    if (window.confirm('Вы действительно хотите сбросить статистику?')) {
      stats.resetStats();
      alert('Статистика успешно сброшена!');
    }
  }


  getFileDescription() {
    return {
      content: stats.getStatsForExport(),
      name: getFileName('stats', 'json'),
      type: mimeTypes.json
    };
  }


  backToRegularResults() {
    this.props.actions.fetchResults();
  }

}


_AdminStats.propTypes = {
  actions: PropTypes.object
};


function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}


export const AdminStats = connect(null, mapDispatchToProps)(_AdminStats);



