import { padStart } from './text';
import { API } from './../api';


export const mimeTypes = Object.freeze({
  csv: 'text/csv',
  json: 'application/json'
});

/**
 * Выгружает пользователю файл.
 * @see http://jsfiddle.net/UselessCode/qm5AG/
 * @param {object} params
 * @param {string} params.content
 * @param {string} params.type MIME-type.
 * @param {string} [params.charset='utf-8']
 * @param {object} [params.fileObject] Ранее сгенерированный объект для выгрузки этого файла.
 * @returns {File|Blob} See URL.createObjectURL API.
 */
export function getFileObject(params) {
  releaseFileObject(params.fileObject);

  const charset = params.charset || 'utf-8';
  const type = `data:${params.type};charset=${charset}`;
  const data = new Blob([params.content], { type });
  const fileObject = window.URL.createObjectURL(data);

  return fileObject;
}

/**
 * @see http://jsfiddle.net/UselessCode/qm5AG/
 * @param {object} [fileObject] Ранее сгенерированный объект для выгрузки этого файла.
 */
export function releaseFileObject(fileObject) {
  if (fileObject !== null) {
    // If we are replacing a previously generated file we need to
    // manually revoke the object URL to avoid memory leaks.
    window.URL.revokeObjectURL(fileObject);
  }
}


/**
 * Запрашивает у пользователю разделитель для значений в CSV.
 * @param {string} [defaultDelimiter=';']
 * @returns {string}
 */
export function getCSVDelimiter(defaultDelimiter = ';') {
  const userDelimiter = prompt(`Введите разделитель для CSV.\n`
    + `По умолчанию будет использован символ "${defaultDelimiter}".`, defaultDelimiter);
  return userDelimiter || defaultDelimiter;
}


/**
 * Читает с диска файл.
 * @param {File} file Объект File, который может быть получен из input/type=file.
 * @returns {Promise<string>}
 */
export function readFileAsString(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();

    reader.onload = (event) => {
      const content = event.target.result;
      resolve(content);
    };

    reader.onerror = (error) => {
      reject(error);
    };

    reader.readAsText(file);
  });
}


/**
 * Получение timestamp для использования в именах файлов, например.
 * @returns {string}
 */
export function getTimestamp() {
  const now = new Date();
  const y = now.getFullYear();
  const m = padStart(now.getMonth() + 1, 2, '0');
  const d = padStart(now.getDate(), 2, '0');
  const H = padStart(now.getHours(), 2, '0');
  const M = padStart(now.getMinutes(), 2, '0');
  const S = padStart(now.getSeconds(), 2, '0');
  return `${y}-${m}-${d}--${H}-${M}-${S}`;
}


export function getFileName(section, extension) {
  const timestamp = getTimestamp();
  const gameId = API.getGameID();
  const fileName = `${gameId}-${section}-${timestamp}.${extension}`;
  return fileName;
}



