import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { getDeclension } from './../../helpers'
import './UserResult.css';
import { MainImage } from './../MainImage';
import { Button } from './../Button';
import { finalStates, getFinalStateByScores, l10n } from './../../helpers';
import resultByFinalState from './results-by-scores';


export class UserResult extends Component {

  render() {
    const { userInfo } = this.props;
    const resultData = this.getResultData(userInfo);

    const texts = l10n.getTexts('aValue', 'bValue');

    return <div className="block UserResult">
      <MainImage sticker={ resultData.sticker }/>

      <div className="indent_vert UserResult__title">
        Поздравляем!
      </div>

      <div className="indent_vert UserResult__text">
        { resultData.text }
      </div>

      <div className="indent_vert UserResult__values">
        Вы набрали { this.renderValue(userInfo.aValue) }
        &nbsp;за&nbsp;{ texts.aValue.toLowerCase() }
        &nbsp;и&nbsp;{ this.renderValue(userInfo.bValue) }
        &nbsp;за&nbsp;{ texts.bValue.toLowerCase() }.
      </div>

      <div className="indent_vert_double UserResult__actions">
        <Button text="Результаты" onPress={this.props.onShowResults}/>
        <Button text="Играть ещё раз" onPress={this.props.onTryAgain}/>
      </div>
    </div>;
  }


  renderValue(value) {
    const valueModifier = value < 0
      ? 'UserResult__value_neg'
      : (value === 0 ? 'UserResult__value_zero' : 'UserResult__value_pos');
    const valueClass = `UserResult__value`;
    return <span className={valueClass}>
      <span className={valueModifier}>
        { value }
      </span>&nbsp;{ getDeclension(value, 'балл', 'балла', 'баллов') }
    </span>;
  }


  getResultData(userInfo) {
    const forcedFinalState = userInfo.forcedFinalState;
    const finalState = (!forcedFinalState || finalStates.auto === forcedFinalState)
      ? getFinalStateByScores(userInfo.aValue, userInfo.bValue)
      : forcedFinalState;
    const result = resultByFinalState[finalState];
    return result;
  }

}


UserResult.propTypes = {
  userInfo: PropTypes.object,
  userResult: PropTypes.object,
  onShowResults: PropTypes.func,
  onTryAgain: PropTypes.func
};



