import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './FileExport.css';
import { Button } from './../Button';
import { releaseFileObject, getFileObject } from './../../helpers';


/**
 * @typedef ExportedFileDescription
 * @property {string} content Содержимое файла.
 * @property {stirng} name Имя файла.
 * @property {string} type MIME-тип файла.
 */


export class FileExport extends Component {

  constructor(props) {
    super(props);

    this.state = {
      fileObject: null,
      fileName: null
    };
  }

  render() {
    const { prepareText, downloadText } = this.props;
    const { fileObject, fileName } = this.state;

    return <div className="FileExport">
      <Button text={prepareText}
        onPress={this.prepareFile.bind(this)}
      />
      { fileObject
        && <Button className="FileExport FileExport__file"
          text={downloadText}
          type="link"
          href={fileObject}
          downloadFileName={fileName}
        />
      }
    </div>;
  }


  prepareFile() {
    const fileDescription = this.props.getFileDescription();
    this.releaseStateFileObject();
    this.setState({
      fileObject: getFileObject({
        content: fileDescription.content,
        type: fileDescription.type
      }),
      fileName: fileDescription.name
    });
  }


  componentWillUnmount() {
    this.releaseStateFileObject();
  }


  releaseStateFileObject() {
    releaseFileObject(this.state.fileObject);
  }

}


FileExport.propTypes = {
  prepareText: PropTypes.string,
  downloadText: PropTypes.string,
  /**
   * Функция должна возвращать описание экспортируемого файла.
   * @returns {ExportedFileDescription}
   */
  getFileDescription: PropTypes.func
};



