export const finalStates = Object.assign({
  auto: 'auto',
  negNeg: 'negNeg',
  negPos: 'negPos',
  posNeg: 'posNeg',
  posPos: 'posPos'
});


export function getFinalStateByScores(aValue, bValue) {
  const aPositive = aValue >= 0;
  const bPositive = bValue >= 0;
  if (aPositive && bPositive) {
    return finalStates.posPos;
  } else if (aPositive && !bPositive) {
    return finalStates.posNeg;
  } else if (!aPositive && bPositive) {
    return finalStates.negPos;
  } else {
    return finalStates.negNeg;
  }
}



