const statsStorageKey = 'stats';
const PREFIX = 'STATS';


/**
 * @typedef StatsObject
 * @property {object[]} qa Questions and answers
 * @property {number} qa[].index Индекс вопроса
 * @property {string} qa[].text Текст вопроса для упрощения разбора статистики.
 * @property {number} qa[].answeredTimes Сколько раз ответили на этот вопрос.
 * @property {object[]} qa[].answers Статистика по ответам.
 * @property {number} qa[].answers[].index Индекс ответа.
 * @property {string} qa[].answers[].text Текст ответа для упрощения разбора статистики.
 * @property {number} qa[].answers[].selectedTimes Сколько раз был выбран этот ответ.
 */


class Stats {

  constructor() {
    /**
     * @type {StatsObject}
     */
    this.stats = this._readFromStorage() || {};
  }


  /**
   * Читает статистику, сохранённую в localStorage.
   * @private
   * @returns {StatsObject}
   */
  _readFromStorage() {
    const statsStr = window.localStorage.getItem(statsStorageKey);
    if (!statsStr) {
      return null;
    }

    try {
      return JSON.parse(statsStr);
    } catch (e) {
      console.log(PREFIX, 'error while parsing stats from local storage:');
      console.log(PREFIX, 'value', statsStr);
      console.log(PREFIX, 'error', e);
    }

    return null;
  }


  /**
   * Сохранение выбранного ответа для вопроса в статистике.
   * @param {Question} question
   * @param {Answer} answer
   */
  onAnswer(question, answer) {
    this.stats.qa = this.stats.qa || [];

    let savedQuestion = this.stats.qa.filter(q => q.index === question.__index)[0];
    if (!savedQuestion) {
      const answers = question.answers.map((a, index) => ({
        index,
        text: a.text,
        selectedTimes: 0
      }));
      const newQuestion = {
        index: question.__index,
        text: question.text,
        answeredTimes: 0,
        answers
      };
      this.stats.qa.push(newQuestion);
      savedQuestion = newQuestion;
    }

    savedQuestion.answeredTimes++;

    const savedAnswer = savedQuestion.answers.filter(a => a.index === answer.__index)[0];
    if (savedAnswer) {
      savedAnswer.selectedTimes++;
    } else {
      console.log(PREFIX, 'unexpected answer', answer);
    }


    this.saveStatsToStorage();
  }


  saveStatsToStorage() {
    const statsStr = JSON.stringify(this.stats);
    window.localStorage.setItem(statsStorageKey, statsStr);
  }


  /**
   * Получение строки для экспорта статистики.
   * @returns {string}
   */
  getStatsForExport() {
    const statsStr = JSON.stringify(this.stats, null, 4);
    return statsStr;
  }


  resetStats() {
    this.stats = {};
    window.localStorage.removeItem(statsStorageKey);
  }

}


export const stats = new Stats();



