import { questions } from './questions-mock';
import { getMockResults, addMockResult, removeMockResult } from './results-mock';
import { stats } from './stats';


function delay(timeout) {
  return new Promise(resolve => setTimeout(resolve, timeout || 0));
}


const cache = {
  gameId: 'jpoint18',
  adminPassword: 'odklbest',
  questions: null,
  questionsNumber: 0
};


export const API = {

  getAdminPassword() {
    return cache.adminPassword;
  },


  setAdminPassword(password) {
    cache.adminPassword = password;
  },


  getGameID() {
    return cache.gameId
  },


  removeResult(result) {
    return delay().then(() => removeMockResult(result));
  },


  fetchResults() {
    return delay().then(getMockResults);
  },


  fetchQuestion(userInfo, prevQuestion) {
    return delay()
      .then(() => API.fetchQuestions())
      .then((questions) => {
        const nextNum = prevQuestion ? (prevQuestion.__index + 1) : 0;
        const question = questions.filter(q => q.__index === nextNum)[0] || null;
        return question;
      });
  },


  fetchQuestions() {
    if (cache.questions) {
      return Promise.resolve(cache.questions);
    }
    return delay().then(() => {
      cache.questions = this.prepareQuestions(questions);
      cache.questionsNumber = cache.questions.length;
      return cache.questions;
    });
  },


  /**
   * @private
   */
  prepareQuestions(questions) {
    const preparedQuestions = questions.map((question, index) => Object.assign({
      __index: index
    }, question));
    preparedQuestions.forEach(question => {
      question.answers.forEach((answer, index) => {
        answer.__index = index;
      });
    });
    return preparedQuestions;
  },


  applyAnswer(userInfo, question, answer) {
    return delay().then(() => {
      stats.onAnswer(question, answer);

      const updatedUserInfo = Object.assign({}, userInfo);
      const reactionIndex = Math.floor(Math.random() * answer.reactions.length);
      const reaction = answer.reactions[reactionIndex];
      updatedUserInfo.aValue += reaction.aValue;
      updatedUserInfo.bValue += reaction.bValue;
      return {
        userInfo: updatedUserInfo,
        reaction
      };
    });
  },


  fetchUserResult(userInfo, forcedFinalState) {
    return delay().then(() => {
      const updatedUserInfo = Object.assign({}, userInfo);
      updatedUserInfo.score = userInfo.aValue + userInfo.bValue;
      if (forcedFinalState) {
        updatedUserInfo.forcedFinalState = forcedFinalState;
      }

      addMockResult(updatedUserInfo);

      return {
        userInfo: updatedUserInfo
      };
    });
  },


  getQuestionsNumber() {
    return cache.questionsNumber;
  },


  _getUserInfoId(name, phone) {
    const phoneDigits = phone.replace(/\D+/g, '');
    const preparedName = name.trim().toLowerCase().replace(/\s/g, '');
    return `${preparedName}__${phoneDigits}`;
  },


  generateUserInfo(name, phone) {
    return {
      id: API._getUserInfoId(name, phone),
      name,
      phone,
      score: 0,
      aValue: 0,
      bValue: 0
    };
  }

};



