import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { actions } from '../../redux';
import { Button } from './../Button';
import { l10n } from './../../helpers';


class _Administrate extends Component {

  render() {
    const texts = l10n.getTexts('adminResults', 'stickers', 'settings', 'stats', 'backToRegularResults');

    return <div className="block Administrate">
      <div className="indent_vert">
        <Button text={texts.adminResults}
            onPress={this.goToAdminResults.bind(this)}
          ></Button>
      </div>
      <div className="indent_vert">
        <Button text={texts.stickers}
          onPress={this.goToStickers.bind(this)}
        />
      </div>
      <div className="indent_vert">
        <Button text={texts.settings}
          onPress={this.goToAdminSettings.bind(this)}
        />
      </div>
      <div className="indent_vert">
        <Button text={texts.stats}
          onPress={this.goToAdminStats.bind(this)}
        />
      </div>
      <div className="indent_vert">
        <Button text={texts.backToRegularResults}
            onPress={this.goToResults.bind(this)}
          ></Button>
      </div>
    </div>;
  }


  goToAdminStats() {
    this.props.actions.administrateStats();
  }


  goToAdminSettings() {
    this.props.actions.administrateSettings();
  }


  goToResults() {
    this.props.actions.fetchResults();
  }


  goToAdminResults() {
    this.props.actions.administrateResults();
  }


  goToStickers() {
    this.props.actions.showStickers();
  }

}


_Administrate.propTypes = {
  actions: PropTypes.object
};


function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}


export const Administrate = connect(null, mapDispatchToProps)(_Administrate);



